import React, {Component} from "react";
import style from "./Ingredient.module.css";
import PropType from "prop-types";

class Ingredient extends Component{
    render(){
    let ingredient = null; 

    switch(this.props.type){
        case("bread-top"): ingredient = (
            <div className={style.BreadTop}>
                <div className={style.Seeds1}></div>
                <div className={style.Seeds2}></div>
            </div>);
            break;
        case("bread-bottom"): ingredient = <div className={style.BreadBottom}></div>;
            break;
        case("meat"): ingredient = <div className={style.Meat}></div>
            break;
        case("cheese"): ingredient = <div className={style.Cheese}></div>
            break;
        case("salad"): ingredient = <div className={style.Salad}></div>
            break;
        case("bacon"): ingredient = <div className={style.Bacon}></div>
            break;
        default: ingredient = null;
    }
    
    return ingredient;
    }
}

Ingredient.propTypes = {type: PropType.string.isRequired}

export default Ingredient;

